<template>
  <div class="love-floor">
    <div class="tit-img">
      <img src="../assets/images/hometuijian.png" />
    </div>
    <div class="love-floor-list">
      <ul class="find-similar-list">
        <li v-for="(item,index) in list" :key="index">
          <div class="similar">
            <img :src="item.url" @click="add(index)" />
          </div>
          <span class="similar-product-text">
            <img src="../assets/images/618.png" alt />
            {{item.title}}
          </span>
          <p class="similar-product-info">
            <span class="similar-product-price">
              ¥&nbsp;
              <span class="big-price">{{item.price}}</span>
            </span>
            <van-button type="danger" size="small" @click="add(index)">立即购买</van-button>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    add(index) {
      this.$router.push({ name: "productdetails", query: this.list[index] });

      // 添加进我的足迹
      if (this.$store.state.login == 0) {
        return;
      }
      this.$store.commit("addfootprint",this.list[index])
      // console.log(this.list[index])
      
    }
  },
  created() {
    this.list = this.$store.state.list.slice(0, 10);
  }
};
</script>

<style lang="less" scoped>
.love-floor {
  width: 100%;
  background-color: #fafafa;
  > .tit-img {
    width: calc(100% - 20px);
    margin-left: 10px;
    height: 38px;
    > img {
      width: 100%;
      height: 100%;
    }
  }
  > .love-floor-list {
    width: calc(100% - 20px);
    margin: 10px 10px 0 10px;
    height: 100%;
    margin-top: 10px;
    overflow: hidden;
    > .find-similar-list {
      width: 100%;
      height: 100%;
      > li {
        width: calc(50% - 1px);
        height: 250px;
        padding-top: 8px;
        float: left;
        background-color: #ffffff;
        border-radius: 10px;
        > .similar {
          width: 100%;
          > img {
            height: 100%;
            width: 100%;
          }
        }
        > .similar-product-text {
          padding-left: 5px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
          font-size: 13px;
          width: 100%;
          img {
            width: 31px;
            height: 13px;
            margin-right: 10px;
          }
        }
        > .similar-product-info {
          padding-left: 5px;
          > .similar-product-price {
            color: red;
            font-size: 12px;
            padding-top: 15px;
            > .big-price {
              font-size: 17px;
            }
          }
          button {
            float: right;
          }
        }
      }
      > li:nth-child(2n - 1) {
        margin: 1px 1px 2px 0;
      }
      > li:nth-child(2n) {
        margin: 1px 0 2px 1px;
      }
    }
  }
}
</style>