<template>
    <div class="hx-return">
        <div class="return-previous" @click="routefallback">
            <van-icon name="arrow-left" />
        </div>
        <div class="return-title">
            {{title}}
        </div>
        <div class="return-nav">
        </div>
    </div>
</template>

<script>
    export default {
        props: ['title'],
        data(){
            return{

            }
        },
        methods:{
            // 回退上一个路由
            routefallback(){
                if(this.$route.path == '/order/tobepaid' || this.$route.path =='/order/tobereceived' || this.$route.path == '/order/whole'){
                    this.$router.push({name:"user"})
                }else if(this.$route.path=='/login/register' || this.$route.path =='/login/mylogin'){
                    this.$router.push({name:"user"})
                }else{
                this.$router.back(-1)
                }
                
            }
        }
    }
</script>

<style lang="less" scoped>
    .hx-return{
        z-index: 2;
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 43px;
        background-color: #fff;
        border-bottom: 1px solid #f4f4f4;
        >div{
            float: left;
        }
        >.return-previous,.return-nav{
         height: 100%;
         width: 44px;
         text-align: center;
         line-height: 50px; 
         font-size: 22px; 
         color: #62646f; 
        }
        >.return-title{
            height: 100%;
            width: calc(100% - 88px);
            text-align: center;
            line-height: 44px;
            color: black;
        }
    }
</style>