<template>
  <div class="shoppingcart">
    <!-- 自定义回退路由组件 -->
    <hx-Return :title="title"></hx-return>
    
    <!-- 管理 -->
    <div class="shopp-administration" v-show="isadministration" @click="set">
      <div class="administration">管理</div>
    </div>

    <!-- 购物车没有商品显示 -->
    <hx-Empty v-show="!isadministration"></hx-Empty>
    <!-- 订单 -->
    <router-link
      tag="div"
      to="/productdetails"
      class="shopp"
      v-for="(item,index) in lists"
      :key="index"
    >
      <div class="shopp-left">
        <div class="left-checkbox">
          <van-checkbox v-model="item.check" @click="select"></van-checkbox>
        </div>
        <router-link tag="div" to="/productdetails" class="left-img">
          <img :src="item.url" />
        </router-link>
      </div>
      <div class="shopp-right">
        <a href="javascript:void(0)"><img src="../assets/images/618.png" alt="">{{item.title}}</a>
        <div class="price_line">
          <div class="price">
            <div>￥</div>
            <span class="int">{{item.price}}</span>
          </div>
          <div class="num_wrap">
            <van-stepper v-model="item.num" input-width="25px" button-size="25px" />
          </div>
        </div>
        <div class="commodity-del">
          <div class="van-ellipsis">{{item.specifications}}</div>
          <van-icon name="delete" @click="del(index)" class="btn-del" />
        </div>
      </div>
    </router-link>

    <van-divider>可能你还想要</van-divider>
    <div class="shopp-recommended">
      <hx-Recommended></hx-Recommended>
    </div>
    

    <!-- 结算 -->
    <van-submit-bar
      class="shoppingcart-fixbar"
      :price="totalprice"
      button-text="去结算"
      v-show="iswhole"
      @submit="jump"
    >
      <van-checkbox v-model="check" @click="selectall">全选</van-checkbox>
    </van-submit-bar>
    <!-- 删除多个 -->
    <van-submit-bar class="shoppingcart-fixbar" button-text="删除" v-show="isshow" @submit="delall">
      <van-checkbox v-model="check" @click="selectall" class="fixbar-check">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import hxReturn from "../components/hxReturn";
import hxEmpty from "../components/hxEmpty";
import hxRecommended from "../components/hxRecommended";


export default {
  data() {
    return {
      title: "购物车", //标题
      iswhole: false, //提交订单
      isshow:false,  //删除多个
      isadministration:false, //是否有数据
      check: false, //全选
      totalprice: 0, //总价格
      lists: [
        // {
        //   id: 1,
        //   title:
        //     "英特尔酷睿i5 9400f/i7组装电脑主机独显游戏台式机家用电竞吃鸡英雄联盟LOL台式电脑整机全套",
        //   price: "3999",
        //   num: 1,
        //   url: require("../assets/images/quanmianpin.jpg")
        // },
      ]
    };
  },
  methods: {
    // 删除单个
    del(index) {
      this.lists.splice(index, 1);
      if (this.lists.length < 1) {
        this.check = false;
      }
    this.islist();
    },
    //删除多个
    delall() {
    // for (let index = _this.teamIds.length - 1; index >= 0 ; index--) {
    //    if(_this.teamIds[index].indexOf(val) == '-1'){
    //       _this.teamIds.splice(index, 1);
    //    }
    for(let i = this.lists.length -1;i >=0;i--){
      if(this.lists[i].check){
        this.lists.splice(i,1)
      }
    }
    this.islist();
    },
    // 单选
    select() {
      for (let i = 0; i < this.lists.length; i++) {
        if (!this.lists[i].check) {
          this.check = false;
          return;
        }
        this.check = true;
      }
    },
    // 全选
    selectall() {
      this.lists.forEach(v => {
        v.check = this.check;
      });
    },
    // 总价格
    total() {
      // 总价格计算
      // total 累加值
      this.totalprice = this.lists.reduce((totalprice, item) => {
        // 判断选项选中计算价格
        if (!item.check) return totalprice;
        return totalprice + parseInt(item.price) * parseInt(item.num) * 100; //总价格
      }, 0); //总价格默认值0
    },
    // 第一次加载页面过程中，判断复选框状态，全部true，返回true，有一个false,放回false
    checkall() {
      this.check = this.lists.every(item => {
        return item.check;
      });
    },

    // 订单提交
    jump(){
      let obj =[]
      for(let i = this.lists.length -1;i >=0;i--){
      if(this.lists[i].check){
        obj.push(this.lists[i])
        this.lists.splice(i,1)
      }
    }
    // console.log(obj)
    this.$store.state.tobepaidlist =  { obj: obj ,price:this.totalprice}
    this.$router.push({name:"placeorder"})
    },

    // 判断是否有商品数据
    islist(){
    if(this.lists.length == 0){
      this.isadministration = false;
      this.iswhole = false;
    }else{
      this.isadministration = true;
      this.iswhole = true;
    }
    },
    // 管理切换
    set(){
      if(this.iswhole){
        this.iswhole = false;
        this.isshow = true;
      }else{
        this.iswhole = true;
        this.isshow = false;
      }
    }
  },
  created() {
    // 底部导航 选中变化
    if (this.$route.path === "/shoppingcart") {
      this.$store.state.num = 2;
    }
    // 页面滚动距离改为0
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    // 底部导航显示
    this.$store.state.vantTab = true;
    // 数据更新
    this.lists = this.$store.state.cart.lists;
    // 调用方法
    this.total();
    this.checkall();
    this.islist();
  },
  // 数据更新调用
  beforeUpdate() {
    this.total();
  },
  // 组件销毁  数据更新
  beforeDestroy() {
    this.$store.commit("cart/edit", this.lists);
     this.$store.state.cart.nums = this.lists.length;
  },


  components: {
    hxReturn,
    hxEmpty,
    hxRecommended,
  }
};
</script>

<style lang="less" scoped>
.shoppingcart {
  padding-top: 50px;
  height: calc(100% - 100px);
  
  > .shopp-administration {
    width: 100%;
    height: 30px;
    > .administration {
      float: right;
      margin-right: 10px;
      width: 50px;
      height: 30px;
      text-align: center;
      line-height: 40px;
      font-size: 15px;
      cursor: pointer;
    }
  }
  > .shoppingcart-fixbar {
    bottom: 49px;
    border-bottom: 1px solid #f6f6f6;
    .fixbar-check {
      position: absolute;
      left: 16px;
    }
  }
  .shopp {
    width: 100%;
    height: 130px;
    background-color: #ffffff;
    margin-top: 5px;
    margin-bottom: 5px;
    border-radius: 10px;
    overflow: hidden;
    div {
      float: left;
    }
    > .shopp-left {
      width: 35%;
      height: 100%;
      .left-checkbox {
        width: 30px;
        padding-top: 40px;
        padding-left: 10px;
      }
      .left-img {
        width: 80px;
        height: 80px;
        > img {
          width: 100%;
          height: 100%;
          margin-top: 10px;
        }
      }
    }
    > .shopp-right {
      width: 65%;
      height: 120px;
      padding-top: 10px;
      > a {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 15px;
        width: (100% - 10px);
        margin-left: 10px;
        color: black;
        img{
           width: 31px;
              height: 13px;
              margin-right: 10px;
        }
      }
      > .price_line {
        margin: 10px;
        width: 100%;
        > .price {
          color: red;
          font-size: 12px;
          > div {
            position: relative;
            top: 5px;
          }
          > .int {
            font-size: 18px;
          }
        }
        > .num_wrap {
          float: right;
          margin-right: 30px;
        }
      }
      > .commodity-del {
        float: right;
        margin-right: 25px;
        width: 80%;
        div{
          float: left;
          font-size: 13px;
          width: 150px;
        }
         .btn-del {
          float: right;
        }
      }
    }
  }
  .shopp-recommended{
    margin-bottom: 99px;
  }
}
</style>